.countHandler input{
    width: 40px;
    text-align: center;
    font-weight: bolder;
}
.checkout button{
    width: 150px;
    height: 50px;
    background-color: rgb(19,19,19);
    color: white;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
}
.cart{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.cartItem{
    width: 700px;
    height: 250px;
    display: flex;
    align-items: center;
    box-shadow: 0px 3px 15px rgb(0,0,0,0.5);
    border-radius: 25px;
    margin: 30px;
}
.cartItem img{
    width: 200px;
}
.cartItem .decscription{
    width: 100%;
    font-size: 30px;

}
